<template>
  <div class="hello">
    <mt-popup ref="popup" v-model="popupVisible" popup-transition="popup-fade">
      <video-player ref="videoPlayer" :playsinline="true" :x5-playsinline="true" class="test2 video-player vjs-custom-skin" :options="playerOptions"></video-player>
    </mt-popup>
    <div class="test1">
      <mt-button class="btn" @click.native="tovideo">Go to VIDEO</mt-button>
      <mt-button class="btn" @click.native="todetail">Go to Details</mt-button>
    </div>
  </div>
</template>
<script>
import 'video.js/dist/video-js.css'
import 'vue-video-player/src/custom-theme.css'
export default {
  name: 'Home',
  data () {
    return {
      popupVisible: false,
      playerOptions: {
        playbackRates: [0.7, 1.0, 1.5, 2.0], // 播放速度
        autoplay: false, // 如果true,浏览器准备好时开始回放。
        controls: true, // 控制条
        preload: 'auto', // 视频预加载
        muted: false, // 默认情况下将会消除任何音频。
        loop: false, // 导致视频一结束就重新开始。
        language: 'zh-CN',
        aspectRatio: '16:9', // 将播放器置于流畅模式，并在计算播放器的动态大小时使用该值。值应该代表一个比例 - 用冒号分隔的两个数字（例如"16:9"或"4:3"）
        fluid: true, // 当true时，Video.js player将拥有流体大小。换句话说，它将按比例缩放以适应其容器。
        sources: [{
          type: 'video/mp4',
          src: 'http://vjs.zencdn.net/v/oceans.mp4'
        }],
        poster: 'https://surmon-china.github.io/vue-quill-editor/static/images/surmon-1.jpg', // 你的封面地址
        width: document.documentElement.clientWidth,
        notSupportedMessage: '此视频暂无法播放，请稍后再试' // 允许覆盖Video.js无法播放媒体源时显示的默认信息。
      }
    }
  },
  watch: {
    popupVisible: function (val) {
      if (val) {
        console.log('aa')
      } else {
        console.log('bb')
        this.$refs.videoPlayer.player.pause()
      }
    }
  },
  methods: {
    tovideo () {
      // this.$router.togo('/VideoPlayer')
      this.popupVisible = true
    },
    todetail () {
      this.$router.togo('/Details')
    }
  }
}

</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hello {
  width: 100%;
  height: 100%;
  background-image: url(../assets/121.jpg);
  background-size: cover;
  position: fixed;
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-content: center;
}

.test1 {
  display: flex;
  flex-flow: column;
  justify-content: flex-end;
  align-content: flex-end;
}

.test2 {
  width: 10rem;
}

.btn {
  margin: 0.4rem 1.4rem;
  height: 2rem;
  font-size: 0.4rem;
}

h1 {
  font-size: 40px;
  width: 3.5rem;
  border: 1px solid #fff
}

</style>



